import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { Subject } from 'rxjs';
import { ILibrary } from '../interfaces/library.interface';
import { IBookObject } from '../interfaces/book-object.interface';
// import { Claim } from '../components/generic-modal/generic-modal.component';

// export interface ClaimObject {
//   id: string;
//   claims: Claim[];
// }

@Injectable({
  providedIn: 'root'
})

export class EndpointsService {
  baseUrl = environment.apiUrl;

  public userDataSubjectEvent = new Subject<any>();

  subsricption: Subject<ILibrary> = new Subject();

  constructor(private http: HttpClient) { }


  getLibraries() {
    return this.http.get<ILibrary[]>(this.baseUrl + 'api/libraries');
  }

  getALibrary(libraryId) {
    return this.http.get<ILibrary>(this.baseUrl + 'api/libraries/' + libraryId);
  }

  getAvailableBooks(libraryId) {
    const id = libraryId.toString();
    return this.http.get<IBookObject[]>(this.baseUrl + 'api/libraries/' + id + '/books/available');
  }

  getBooks(libraryId) {
    const id = libraryId.toString();
    return this.http.get<IBookObject[]>(this.baseUrl + 'api/libraries/' + id + '/books');
  }

  setSelectedLibrary(lib: ILibrary) {
    this.subsricption.next(lib);
  }

}


