import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EndpointsService } from 'src/app/services/endpoints.service';
import { ILibrary } from 'src/app/interfaces/library.interface';
import { Subject } from 'rxjs';


@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.scss']
})
export class NavBarComponent implements OnInit {
  mobileMenuOpen = false;  // Keeps the mobile devices menu closed until triggered
  selectedLibrary = { name: 'Toronto Public Library'};  // Initializes the selected library
  searchValue = 'Clear';  // Place holder for the search term in the search box
  showSearchInput = false; // Initially hides the search input box until the search icon is clicked.
  libraries: ILibrary[];

  // Inject the router, so as to use it to trigger a new route to navigate the user to.
  constructor(private router: Router, private endpointsService: EndpointsService) { }

  ngOnInit() {
    this.endpointsService.getLibraries().subscribe((libraries: ILibrary[]) => {
      this.libraries = libraries;
    });
  }


  loadSelectedLibrary(libraryId: number) {
    const id = libraryId.toString();
    this.endpointsService.getALibrary(id).subscribe((library: ILibrary) => {
      if (this.router.url.split('/')[1] === 'libraries') {
        this.endpointsService.setSelectedLibrary(library);
        this.router.navigate(['/libraries', id]);
      } else {
        this.router.navigate(['/libraries', id], {state: {data: library}});
      }
    });
  }


  // When called toggles the mobile devices menu
  toggleMobileMenu() {
    this.mobileMenuOpen = !this.mobileMenuOpen;
  }

  // when called, chooses the library, and then closes the mobile menu
  getSelectedLibrary(library) {
    this.mobileMenuOpen = false;
    this.loadSelectedLibrary(library.libraryId);
    this.selectedLibrary = library;
  }


  // When called, toggles the search input box.
  toggleSearch() {
    this.showSearchInput = !this.showSearchInput;
  }

  // When called, clears the search input field.
  clearSearchTerm() {
    this.searchValue = '';
  }

  // Search toggle helper method
  closeAndToggleSearch() {
    this.mobileMenuOpen = false;
    this.toggleSearch();
  }

  // Route navigation helper method.
  navigatetoLink(link) {
    this.mobileMenuOpen = false;
    this.router.navigate([link]);
  }

}
