import { Component, OnInit, OnDestroy, TemplateRef } from '@angular/core';
import { ILibrary } from 'src/app/interfaces/library.interface';
import { EndpointsService } from 'src/app/services/endpoints.service';
import { ActivatedRoute } from '@angular/router';
import { IBookObject } from './../../interfaces/book-object.interface';
import { MatDialog } from '@angular/material';

@Component({
  selector: 'app-library',
  templateUrl: './library.component.html',
  styleUrls: ['./library.component.scss']
})
export class LibraryComponent implements OnInit, OnDestroy {

  library: ILibrary;
  availableBooks: IBookObject[];
  isGetAvailableBooks = false;
  currentSelectedItem: IBookObject;

  constructor(private endpointsService: EndpointsService, private route: ActivatedRoute, public dialog: MatDialog) { }

  ngOnInit() {
    const routeP = this.route.snapshot.params.libraryId;
    this.library = history.state.data;

    this.endpointsService.subsricption.subscribe((lib: ILibrary) => {
      this.library = lib;
    });

    this.endpointsService.getALibrary(routeP).subscribe((library: ILibrary) => {
      this.library = library;
      // this.isGetAvailableBooks = false;
    });
  }

  getAvailableBooks(id) {
    this.isGetAvailableBooks = true;
    this.endpointsService.getAvailableBooks(id).subscribe((bookObjects: IBookObject[]) => {
      this.availableBooks = bookObjects;
    });
  }

  displaySelectedBookDetails(templateRef: TemplateRef<any>, bookObj: IBookObject) {
      this.currentSelectedItem = bookObj;
      this.dialog.open(templateRef);
  }

  ngOnDestroy() {
    this.endpointsService.subsricption.unsubscribe();
  }

}
