import { Component, OnInit, TemplateRef } from '@angular/core';
import { EndpointsService } from 'src/app/services/endpoints.service';
import { ILibrary } from 'src/app/interfaces/library.interface';
import { Router } from '@angular/router';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

 public libraries: ILibrary[];
 currentSelectedLibrary: ILibrary;

  // Inject the MatDialog reference that would be used to display a modal
  constructor(private endpointsService: EndpointsService, private router: Router) { }

  // Setting up the form validation
  ngOnInit() {
    this.endpointsService.getLibraries().subscribe((libraries: ILibrary[]) => {
      this.libraries = libraries;
    });
  }

  getSelectedLibrary(libraryId: number) {
    const id = libraryId.toString();
    this.endpointsService.getALibrary(id).subscribe((library: ILibrary) => {
      this.currentSelectedLibrary = library;
      console.log(library);
      this.router.navigate(['/libraries', id], {state: {data: library}});
    });
  }

}
